import React, { Component } from 'react';
import SkillSet from './SkillSet';
import Details from './Details';

class About extends Component {
    state = {  
        sSkills: ['VS Code', 'Notepad++', 'Xampp', 'Photoshop'],
        skills: ['HTML 5', 'CSS 3', 'Javascript', 'Jquery', 'ReactJS', 'Angular 7']
    }
    render() { 
        return ( 
            <div className="container">
                <div className="row">
                    <h2 className="mt-2 headerText">
                        <i className="fa fa-quote-left"></i>
                        <span className="spanHeader">Sanjay .R. Vishwakarma <br/> Front End Developer (UI/UX Developer)</span>
                    </h2>
                    <Details Header="Experience In Industry" Detail="Total Experience: " Indepdth="4.8 years"/>
                    <Details Header="T.Y Bsc (Computer Science)" Detail="College Name: " Indepdth="Nagindas Khandwala College (2010 - 2011)"/>
                    <Details Header="Contact" Detail="Email Id: " Indepdth="sanj9069@gmail.com"/>                  
                    <SkillSet skills={this.state.skills} Header="Web Skills"/>
                    <SkillSet skills={this.state.sSkills} Header="Software Skills"/>
                  
                </div>
            </div>
         );
    }
}
 
export default About;